@import-normalize;

@tailwind base;
@tailwind components;
@tailwind utilities;

@import 'styles/scroll';
// @import 'styles/fonts';

html {
  @apply text-gray-900;

  font-size: 62.5%;
}

body {
  @apply bg-body;
  @apply text-body;
  @apply font-body;

  position: relative;
  font-size: 1.8rem;
  line-height: 3rem;
}

* {
  box-sizing: border-box;
}

h1 {
  @apply text-title;
  @apply font-bold;

  margin: 2.4rem 0;
  font-weight: 700;
}

h2 {
  @apply text-title;
  @apply font-bold;

  margin: 1.8rem 0;
}

ul {
  margin-left: 3.2rem;
  list-style-type: circle;
}

p {
  margin: 0 0 1.6rem 0;
}
