.Timeline {
  &__title {
    @apply headline;
  }

  &__events {
    @apply flex--row;
    @apply gap-6;

    & .Event {
      padding: 2.4rem;
      margin: 1.6rem 0;

      @apply bg-card;
      @apply md:max-w-card-mobile;
      @apply 2xl:max-w-card;
      @apply ultra:max-w-card-ultra;
      @apply shadow-md;
      @apply rounded-xl;

      &__title {
        @apply text-title;

        display: flex;
        flex-direction: row;
        align-items: center;
      }

      &__icon {
        margin-right: 1.6rem;
      }

      &__content {
        & .Event__date {
          font-size: 1.4rem;
          margin: 1.6rem 0;

          @apply text-gray-400;
        }
      }
    }
  }
}
