.Card {
  @apply rounded-xl;
  @apply bg-card;
  @apply shadow-md;
  @apply px-24;
  @apply pb-24;
  @apply pt-0;
  @apply md:max-w-card-mobile;
  @apply 2xl:max-w-card;
  @apply ultra:max-w-card-ultra;

  &--decorated {
    @apply border;
    @apply border-b-8;
    @apply border-secondary;
    @apply flex;
    @apply flex-col;
    @apply items-center;
  }
}
