.ContactStrip {
  @apply w-full;
  @apply flex;
  @apply px-24;
  @apply text-contact;
  @apply md:flex-col;
  @apply md:items-end;
  @apply md:gap-1;
  @apply lg:flex-row;
  @apply lg:justify-end;
  @apply lg:gap-10;
  @apply text-gray-500;

  a {
    @apply underline;
    @apply hover:text-col-contact;
  }
}
