.App {
  @apply py-24;
  @apply lg:px-96;
  @apply md:px-96;
}

.Contact {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-bottom: 12%;

  & .headline {
    margin: 0 0 1.6rem 0;
    text-align: center;
  }

  & .item {
    line-height: 3.6rem;

    & .type {
      font-weight: 700;
      margin-right: 1.6rem;
    }

    & a {
      @apply text-col-contact;
      @apply underline;
      @apply hover:text-secondary;
    }
  }
}
