.Hero {
  &__header {
    font-size: 3.6rem;

    @apply font-bold;
    @apply text-tertiary;
    @apply font-title;
  }

  &__headline {
    font-size: 6.8rem;
    margin: 2.4rem 0;
    line-height: 6.8rem;

    @apply font-bold;
    @apply md:max-w-hero-title-mobile;
    @apply lg:max-w-hero-title;
    @apply font-title;
  }

  &__content {
    @apply md:max-w-hero-desc-mobile;
    @apply lg:max-w-hero-desc;
  }
}
