$z-layer--fast: -10;
$z-layer--normal: -20;
$z-layer--slow: -30;

$blur--xs: blur(0.5rem);
$blur--sm: blur(1rem);
$blur--m: blur(1.5rem);

@mixin cover() {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.blobs {
  @include cover();

  background-size: cover;

  &--fast {
    background: no-repeat url('../../assets/blob-fast.svg');
    z-index: $z-layer--fast;
  }

  &--normal {
    background: no-repeat url('../../assets/blob-md.svg');
    z-index: $z-layer--normal;
  }

  &--slow {
    background: no-repeat url('../../assets/blob-slow.svg');
    z-index: $z-layer--slow;
  }
}

.blur {
  @include cover();

  &--level-0 {
    backdrop-filter: $blur--xs;
    z-index: $z-layer--fast + 1;
  }

  &--level-1 {
    backdrop-filter: $blur--sm;
    z-index: $z-layer--fast - 1;
  }

  &--level-2 {
    backdrop-filter: $blur--m;
    z-index: $z-layer--normal - 1;
  }
}
